// All fonts

// Mukta
@font-face {
    font-family: "Mukta Regular";
    src: url(fonts/Mukta/Mukta-Regular.ttf);
}
@font-face {
    font-family: "Mukta Bold";
    src: url(fonts/Mukta/Mukta-Bold.ttf);
}

// Monaco
@font-face {
    font-family: "Monaco";
    src: url(fonts/Monaco/Monaco.ttf);
}

// Staatliches
@font-face {
    font-family: "Staatliches";
    src: url(fonts/Staatliches/Staatliches-regular.ttf);
}

:is(h1, h2, h3, h4, h5, h6),
.a-animate {
    font-family: "Staatliches";
}
.a-animate {
    color: $color-primary;
}
